<template>
    <div class="container">
      <h1>관리자 페이지</h1>
      <ul class="list-group mt-4">
        <li class="list-group-item header-row d-flex justify-content-between align-items-center">
          <span class="prayer-id">ID</span>
          <span class="prayer-title">제목</span>
          <span class="prayer-top">날짜</span>
          <span class="prayer-nickname">닉네임</span>
        </li>

        <li
          v-for="prayer in prayers"
          :key="prayer.id"
          class="list-group-item list-group-item-action d-flex justify-content-between align-items-center"
          @click="goToDetail(prayer.id)"
        >
          <span class="prayer-id">{{ prayer.id }}</span>
          <span class="prayer-title">{{ prayer.title }}</span>
          <span class="prayer-top">{{ prayer.top }}</span>
          <span class="prayer-nickname text-muted">{{ prayer.userNickname }}</span>
        </li>
      </ul>
  
      <nav aria-label="Page navigation mt-4">
        <ul class="pagination justify-content-center mt-3">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <a class="page-link" href="#" @click.prevent="changePage(currentPage - 1)">이전</a>
          </li>
          <li
            v-for="page in totalPages"
            :key="page"
            class="page-item"
            :class="{ active: page === currentPage }"
          >
            <a class="page-link" href="#" @click.prevent="changePage(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <a class="page-link" href="#" @click.prevent="changePage(currentPage + 1)">다음</a>
          </li>
        </ul>
      </nav>
    </div>
  </template>
  
  <script>
  import axios from 'axios';
  
  export default {
    data() {
      return {
        prayers: [],
        currentPage: 1,
        totalPages: 1, // 백엔드에서 전체 페이지 수를 가져와 설정할 예정
        prayersPerPage: 10, // 한 페이지에 보여줄 글 수
      };
    },
    methods: {
      async fetchPrayer(page) {
        try {
          const response = await axios.get(`http://localhost:8080/admin/getAllUserPrayer`, {
            params: {
              page: page-1, 
              size: this.prayersPerPage,
            },
          });
          

          this.prayers = response.data 
          console.log('전체 데이터: ',response.data)
        } catch (error) {
          console.error("Error fetching prayer:", error);
        }
      },
      async getCount(){
        try {
            const response = await axios.get(`http://localhost:8080/admin/getAllUserPrayerCount`);
            this.totalPages = Math.ceil(response.data/this.prayersPerPage);
            console.log('몇 페이지노? ',this.totalPages);
        }catch (error) {
          console.error("Error fetching prayerCount:", error);
        }   
      },
      changePage(page) {
        if (page > 0 && page <= this.totalPages) {
          this.currentPage = page;
          console.log('현재 페이지: ',this.currentPage)
          this.fetchPrayer(this.currentPage); 
        }
      },
      goToDetail(id) {
        console.log('id값 췤: ',id)
        this.$router.push({path: '/prayerDetail', state: {id: id}});
      },
    },
    created() {
      this.fetchPrayer(this.currentPage);
      this.getCount();
    },
  };
  </script>
  
  <style>
  .container {
    margin-top: 20px;
  }

  .header-row {
  background-color: #343a40; /* 어두운 배경색 */
  color: #ffffff; /* 흰색 글자 */
  font-weight: bold; /* 강조 */
  border: 1px solid #dee2e6; /* 테두리 */
  padding: 10px 15px; /* 여유 있는 패딩 */
  border-radius: 5px; /* 부드러운 모서리 */
}


.prayer-id {
  flex: 0 0 50px; 
  text-align: left;
}


.prayer-title {
  flex: 1; 
  text-align: center;
  padding: 0 10px; 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}


.prayer-top {
  flex: 0 0 180px; 
  text-align: center;
}


.prayer-nickname {
  flex: 0 0 150px; 
  text-align: right;
}
</style>

  