<template>
    <div class="container">
      <b-row class="align-items-center">
      <b-col></b-col>
      <b-col class="text-center">
        <h2 class="mt-5" @click="moveToGroupPrayer">내 기도 목록</h2>
      </b-col>
      <b-col class="text-right">
        <b-button 
          @click="moveToSavePage()" 
          squared 
          type="submit" 
          variant="primary">
          기도 등록하기
        </b-button>
      </b-col>
    </b-row>

      <ul class="list-group mt-4">
        <li class="list-group-item header-row d-flex justify-content-between align-items-center">
          <span class="prayer-id">ID</span>
          <span class="prayer-title">제목</span>
          <span class=".prayer-likes">좋아요</span>
          <span class="prayer-top">날짜</span>
        </li>

        <li
          v-for="prayer in prayers"
          :key="prayer.id"
          class="list-group-item list-group-item-action d-flex justify-content-between align-items-center"
          @click="goToDetail(prayer.id)"
        >
        <span class="prayer-id">{{ prayer.id }}</span>
          <span class="prayer-title">{{ prayer.title }}</span>
          <span class=".prayer-likes">{{ prayer.likesCount }}</span>
          <span class="prayer-top">{{ prayer.top }}</span>
        </li>
      </ul>
  
      <!-- 페이지네이션 -->
      <nav aria-label="Page navigation mt-5">
        <ul class="pagination justify-content-center mt-3">
          <li class="page-item" :class="{ disabled: currentPage === 1 }">
            <a class="page-link" href="#" @click.prevent="changePage(currentPage - 1)">이전</a>
          </li>
          <li
            v-for="page in totalPages"
            :key="page"
            class="page-item"
            :class="{ active: page === currentPage }"
          >
            <a class="page-link" href="#" @click.prevent="changePage(page)">{{ page }}</a>
          </li>
          <li class="page-item" :class="{ disabled: currentPage === totalPages }">
            <a class="page-link" href="#" @click.prevent="changePage(currentPage + 1)">다음</a>
          </li>
        </ul>
      </nav>
    </div>
  </template>

<script>
import axios from 'axios';

export default {
  data() {
    return {
      prayers: [],
      prayersOfWeek: [],
      currentPage: 1,
      totalPages: 1, 
      prayersPerPage: 10, 
      likes: 0,
    };
  },
  methods: {
    async getPrayer(page) {
      try {
        const response = await axios.get(`http://localhost:8080/getMyPrayerList`, {
          params: {
            page: page-1, 
            size: this.prayersPerPage, 
          },
        });
        
        this.prayers = response.data 
        console.log('전체 데이터: ',response.data)
      } catch (error) {
        console.error("Error fetching prayer:", error);
      }
    },
    async getCount(){
      try {
          const response = await axios.get(`http://localhost:8080/getMyPrayerCount`);
          this.totalPages = Math.ceil(response.data/this.prayersPerPage);
          console.log('몇 페이지노? ',this.totalPages);
      }catch (error) {
        console.error("Error fetching prayerCount:", error);
      }   
    },
    changePage(page) {
      if (page > 0 && page <= this.totalPages) {
        this.currentPage = page;
        console.log('현재 페이지: ',this.currentPage)
        this.getPrayer(this.currentPage); 
      }
    },
    goToDetail(id) {
      console.log('id값 췤: ',id)
      this.$router.push({path: '/prayerDetail', state: {id: id}});
    },
    moveToGroupPrayer(){
        this.$router.push({path: '/groupPrayer'});
    },
    moveToSavePage(){
      console.log('sdfsdfdsf')
      this.$router.push({path: '/savePrayer'});
    },
  },
  created() {
    this.getPrayer(this.currentPage);
    this.getCount();
  },
};
</script>

<style>
.container {
  margin-top: 20px;
}

.header-row {
  background-color: #343a40; 
  color: #ffffff;
  font-weight: bold;
  border: 1px solid #dee2e6; 
  padding: 10px 15px; 
  border-radius: 5px;
}


.prayer-id {
  flex: 0 0 50px; 
  text-align: left;
}


.prayer-title {
  flex: 1; 
  text-align: center;
  padding: 0 10px; 
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}


.prayer-top {
  flex: 0 0 180px; 
  text-align: center;
}

.prayer-likes {
  flex: 0 0 50px; 
  text-align: center;
}

</style>